import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import call from '../images/call.png';
import chart from '../images/chart.png';
import logo from '../images/logo.png';

function Header() {
  // dispatcher.
  const changeState = useDispatch();

  // chave para renderizao do componente.
  const viewHeader = useSelector((state) => state.header);

  // login, setor e usurio.
  const loginUnidade = useSelector((state) => state.loginUnidade);
  const loginSetor = useSelector((state) => state.loginSetor);
  const loginUsuario = useSelector((state) => state.loginUsuario);

  // ao para retornar ao componente 'Login'.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };

  // ao para retornar ao componente 'Lista'.
  const clickVoltar = () => {
    changeState({
      type: 'REFRESH_LIST',
      payloadUnidade: loginUnidade,
      payloadSetor: loginSetor,
      payloadUsuario: loginUsuario,
    });
  };

  // ao para acessar o componente 'Password'.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      payloadUnidade: loginUnidade,
      payloadSetor: loginSetor,
      payloadUsuario: loginUsuario,
    });
  };

  // ao para ativao do componente 'Chart'.
  const clickChartButton = () => {
    changeState({
      type: 'CHART_ON',
      payloadUnidade: loginUnidade,
      payloadUsuario: loginUsuario,
      payloadSetor: loginSetor,
    });
  };

  // ao para ativao do componente 'Call'.
  const openCall = () => {
    changeState({
      type: 'CALL_ON',
    });
  };

  // renderizao dos componentes (HTML).
  if (viewHeader === 1) {
    return (
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-between',
          height: '20vh',
          backgroundColor: '#F8F9F9',
          borderRadius: 5,
          marginTop: 5,
          marginBottom: 5,
          marginLeft: 5,
          marginRight: 5,
        }}
      >
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            marginLeft: 12,
            marginRight: 12,
          }}
        >
          <div style={{ backgroundColor: '#F2F4F4', borderRadius: 5 }}>
            <img
              alt=""
              src={logo}
              style={{
                height: 80,
                marginLeft: 10,
                marginRight: 10,
                marginTop: 10,
                marginBottom: 10,
              }}
            ></img>{' '}
          </div>
          <h1
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'flex-end',
              color: '#2c3e50',
              //height: 60,
              marginLeft: 30,
              marginRight: 30,
              marginTop: 5,
              marginBottom: 5,
              borderRadius: 5,
            }}
          >
            ACOLHIMENTO: {loginUnidade}
          </h1>
        </div>
        <div className="main" style={{ flexDirection: 'row' }}>
          <div className="main">
            <button
              tabIndex="-1"
              className="button-ok"
              title="ALTERAR SENHA."
              onClick={() => clickPassword()}
            >
              USURIO: {loginUsuario}
            </button>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <button
                tabIndex="-1"
                className="button-ok"
                style={{ marginTop: 5 }}
                title="RETORNAR  LISTA DE PACIENTES."
                onClick={() => clickVoltar()}
              >
                VOLTAR
              </button>
              <button
                tabIndex="-1"
                className="button-delete"
                style={{ marginTop: 5, marginLeft: 5 }}
                title="FAZER LOGOFF."
                onClick={() => clickLogin()}
              >
                SAIR
              </button>
            </div>
          </div>
          <div>
            <img
              onClick={() => clickChartButton()}
              alt=""
              src={chart}
              style={{ width: 70, height: 70 }}
            ></img>
            <img
              onClick={() => openCall()}
              alt=""
              src={call}
              style={{ width: 70, height: 70 }}
            ></img>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Header;
