import React from 'react';
import axios from 'axios';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';

function Password() {
  // endereos do server.
  var html = 'https://priority-server.herokuapp.com';
  //var html = '//localhost:3001';

  // dispatcher.
  const changeState = useDispatch();

  // chave para renderizao do componente.
  const viewPassword = useSelector((state) => state.password);

  // login, setor e usurio.
  const loginUnidade = useSelector((state) => state.loginUnidade);
  const loginUsuario = useSelector((state) => state.loginUsuario);
  const loginSetor = useSelector((state) => state.loginSetor);
  var senha = '';

  // funo que busca um registro de login vlido no servidor.
  const updateSenha = () => {
    senha = document.getElementById('inputSenha').value;
  };

  // atualizao da senha.
  const sendData = () => {
    // eslint-disable-next-line
    if (senha == '') {
      swal({
        title: 'ERRO',
        text: 'Por favor, insira uma senha!',
        icon: 'warning',
        dangerMode: true,
      });
      changeState({
        type: 'PASSWORD_ON',
        payloadUnidade: loginUnidade,
        payloadUsuario: loginUsuario,
        payloadSetor: loginSetor,
      });
    } else {
      // atualizando o registro de login.
      var obj = {
        usuario: loginUsuario,
        senha: senha,
      };
      axios.post(html + "/updatepassword/'" + loginUsuario + "'", obj);
      swal('SUCESSO', 'Senha alterada com sucesso!', 'success');
      // gatilho para abertura do componente 'List' e atualizao das listas.
      setTimeout(() => {
        changeState({
          type: 'TRIGGER_OFF',
          payloadUnidade: loginUnidade,
          payloadUsuario: loginUsuario,
          payloadSetor: loginSetor,
        });
      }, 1000);
    }
  };

  // renderizao dos componentes (HTML).
  if (viewPassword === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          height: '75vh',
        }}
      >
        <p className="title2" style={{ fontSize: 24, color: 'red' }}>
          USURIO: {loginUsuario}
        </p>
        <label className="label" style={{ marginTop: 20, marginBottom: 10 }}>
          POR FAVOR, INSIRA A NOVA SENHA:
        </label>
        <input
          className="input"
          onChange={() => updateSenha()}
          autoComplete="off"
          placeholder="NOVA SENHA..."
          type="password"
          id="inputSenha"
          style={{
            width: 200,
            height: 50,
          }}
        ></input>
        <p
          className="title2"
          style={{ marginTop: 10, fontSize: 14, width: 500 }}
        >
          PROCURE USAR PELO MENOS 6 CARACTERES, INCLUINDO NMEROS, LETRAS
          MAISCULAS E MINSCULAS.
        </p>
        <button
          onClick={() => sendData()}
          className="button-ok"
          style={{
            width: 80,
            marginTop: 30,
          }}
        >
          OK
        </button>
      </div>
    );
  } else {
    return null;
  }
}
export default Password;
