import { Button } from 'react-bootstrap';
import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Page, Text, View, Document } from '@react-pdf/renderer';
import { PDFViewer } from '@react-pdf/renderer';

function Pdf() {
  // dispatcher.
  const changeState = useDispatch();

  // chave para renderizao do compoenente.
  const viewPdf = useSelector((state) => state.pdf);

  // login, setor e usurio.
  const loginUnidade = useSelector((state) => state.loginUnidade);
  const loginUsuario = useSelector((state) => state.loginUsuario);
  const loginSetor = useSelector((state) => state.loginSetor);

  // recuperando dados do registro classificado.
  const nome = useSelector((state) => state.itemName);
  const doc = useSelector((state) => state.itemDoc);
  const cor = useSelector((state) => state.itemColor);
  const queixa = useSelector((state) => state.itemQueixa);
  const motivoGlasgow = useSelector((state) => state.motivoGlasgow);
  const motivoFc = useSelector((state) => state.motivoFc);
  const motivoPas = useSelector((state) => state.motivoPas);
  const motivoTax = useSelector((state) => state.motivoTax);
  const motivoSaO2 = useSelector((state) => state.motivoSaO2);
  const motivoFr = useSelector((state) => state.motivoFr);

  // funo que renderiza a cor do paciente triado no PDF.
  const [colorRisk, setColorRisk] = useState([]);
  const selectColor = () => {
    if (cor === 'AZUL') {
      setColorRisk('AZUL');
    } else if (cor === 'VERDE') {
      setColorRisk('VERDE');
    } else if (cor === 'AMARELO') {
      setColorRisk('AMARELO');
    } else if (cor === 'LARANJA') {
      setColorRisk('LARANJA');
    } else if (cor === 'VERMELHO') {
      setColorRisk('VERMELHO');
    }
  };

  // ao para retornar ao componente 'List'.
  const showList = () => {
    changeState({
      type: 'TRIGGER_ON',
      payloadUnidade: loginUnidade,
      payloadSetor: loginSetor,
      payloadUsuario: loginUsuario,
    });
  };

  // funo que compe o elemento identificador da cor triada no PDF.
  function ColorText() {
    // eslint-disable-next-line
    if (colorRisk == 'AZUL') {
      return (
        <View
          style={{
            marginTop: 10,
            marginLeft: 10,
            marginRight: 10,
            marginBottom: 10,
            textAlign: 'center',
            backgroundColor: 'blue',
            borderRadius: 5,
          }}
        >
          <Text
            style={{
              color: '#FFFFFF',
              fontSize: 14,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            CLASSIFICAO DE RISCO:
          </Text>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 20,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {cor}
          </Text>
        </View>
      );
      // eslint-disable-next-line
    } else if (colorRisk == 'VERDE') {
      return (
        <View
          style={{
            marginTop: 10,
            marginLeft: 10,
            marginRight: 10,
            marginBottom: 10,
            textAlign: 'center',
            backgroundColor: 'green',
            borderRadius: 5,
          }}
        >
          <Text
            style={{
              color: '#000000',
              fontSize: 14,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            CLASSIFICAO DE RISCO:
          </Text>
          <Text
            style={{
              color: '#000000',
              fontWeight: 'bold',
              fontSize: 20,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {cor}
          </Text>
        </View>
      );
      // eslint-disable-next-line
    } else if (colorRisk == 'AMARELO') {
      return (
        <View
          style={{
            marginTop: 10,
            marginLeft: 10,
            marginRight: 10,
            marginBottom: 10,
            textAlign: 'center',
            backgroundColor: 'yellow',
            borderRadius: 5,
          }}
        >
          <Text
            style={{
              color: '#000000',
              fontSize: 14,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            CLASSIFICAO DE RISCO:
          </Text>
          <Text
            style={{
              color: '#000000',
              fontWeight: 'bold',
              fontSize: 20,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {cor}
          </Text>
        </View>
      );
      // eslint-disable-next-line
    } else if (colorRisk == 'LARANJA') {
      return (
        <View
          style={{
            marginTop: 10,
            marginLeft: 10,
            marginRight: 10,
            marginBottom: 10,
            textAlign: 'center',
            backgroundColor: 'orange',
            borderRadius: 5,
          }}
        >
          <Text
            style={{
              color: '#000000',
              fontSize: 14,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            CLASSIFICAO DE RISCO:
          </Text>
          <Text
            style={{
              color: '#000000',
              fontWeight: 'bold',
              fontSize: 20,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {cor}
          </Text>
        </View>
      );
      // eslint-disable-next-line
    } else if (colorRisk == 'VERMELHO') {
      return (
        <View
          style={{
            marginTop: 10,
            marginLeft: 10,
            marginRight: 10,
            marginBottom: 10,
            textAlign: 'center',
            backgroundColor: 'red',
            borderRadius: 5,
          }}
        >
          <Text
            style={{
              color: '#FFFFFF',
              fontSize: 14,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            CLASSIFICAO DE RISCO:
          </Text>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 20,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {cor}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  // funes que renderizam os alertas na etiqueta.
  function AlertaNone() {
    // eslint-disable-next-line
    if (
      // eslint-disable-next-line
      motivoGlasgow == 'x' &&
      // eslint-disable-next-line
      motivoFc == 'x' &&
      // eslint-disable-next-line
      motivoPas == 'x' &&
      // eslint-disable-next-line
      motivoTax == 'x' &&
      // eslint-disable-next-line
      motivoSaO2 == 'x' &&
      // eslint-disable-next-line
      motivoFr == 'x'
    ) {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {'SEM ALERTAS'}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  function AlertaGlasgow() {
    // eslint-disable-next-line
    if (motivoGlasgow != 'x') {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {motivoGlasgow}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  function AlertaFc() {
    // eslint-disable-next-line
    if (motivoFc != 'x') {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {motivoFc}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  function AlertaPas() {
    // eslint-disable-next-line
    if (motivoPas != 'x') {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {motivoPas}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  function AlertaTax() {
    // eslint-disable-next-line
    if (motivoTax != 'x') {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {motivoTax}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  function AlertaSaO2() {
    // eslint-disable-next-line
    if (motivoSaO2 != 'x') {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {motivoSaO2}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  function AlertaFr() {
    // eslint-disable-next-line
    if (motivoFr != 'x') {
      return (
        <View>
          <Text
            style={{
              color: '#FFFFFF',
              fontWeight: 'bold',
              fontSize: 12,
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {motivoFr}
          </Text>
        </View>
      );
    } else {
      return null;
    }
  }

  // funo que cria o documento PDF.
  const MyDocument = () => (
    <Document>
      <Page
        size="A4"
        style={{
          flexDirection: 'column',
          backgroundColor: '#FFFFFF',
          width: '100%',
          height: '100%',
          orientation: 'portrait',
        }}
      >
        <View
          style={{
            marginTop: 20,
            marginLeft: 20,
            marginRight: 20,
            marginBottom: 20,
            textAlign: 'center',
            backgroundColor: '#E5E7E9',
            borderRadius: 5,
          }}
        >
          <Text
            style={{
              fontSize: 14,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            NOME DO PACIENTE:
          </Text>
          <Text
            style={{
              color: 'red',
              fontWeight: 'bold',
              fontSize: 20,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {nome}
          </Text>
          <Text
            style={{
              fontSize: 10,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            DOCUMENTO:
          </Text>
          <Text
            style={{
              color: 'red',
              fontWeight: 'bold',
              fontSize: 16,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 5,
              marginRight: 5,
            }}
          >
            {doc}
          </Text>
          <ColorText></ColorText>
          <View
            style={{
              flex: 1,
              flexDirection: 'row',
              justifyContent: 'center',
              marginTop: 5,
              marginLeft: 5,
              marginRight: 5,
              marginBottom: 5,
            }}
          >
            <View
              style={{
                flex: 2,
                flexDirection: 'column',
                justifyContent: 'center',
                backgroundColor: '#B2BABB',
                borderRadius: 5,
                minHeight: 50,
                marginTop: 5,
                marginBottom: 5,
                marginLeft: 5,
                marginRight: 5,
              }}
            >
              <Text
                style={{
                  color: '#FFFFFF',
                  fontWeight: 'bold',
                  fontSize: 14,
                  marginTop: 5,
                  marginBottom: 5,
                  marginLeft: 5,
                  marginRight: 5,
                }}
              >
                {'QUEIXA: ' + queixa}
              </Text>
            </View>
            <View
              style={{
                flex: 1,
                flexDirection: 'column',
                justifyContent: 'center',
                backgroundColor: '#E59866',
                borderRadius: 5,
                minHeight: 50,
                marginTop: 5,
                marginBottom: 5,
                marginLeft: 5,
                marginRight: 5,
              }}
            >
              <Text
                style={{
                  color: '#FFFFFF',
                  fontWeight: 'bold',
                  fontSize: 14,
                  marginTop: 5,
                  marginBottom: 0,
                  marginLeft: 5,
                  marginRight: 5,
                }}
              >
                {'ALERTAS:'}
              </Text>
              <View style={{ marginBottom: 5 }}>
                <AlertaNone></AlertaNone>
                <AlertaGlasgow></AlertaGlasgow>
                <AlertaFc></AlertaFc>
                <AlertaPas></AlertaPas>
                <AlertaTax></AlertaTax>
                <AlertaSaO2></AlertaSaO2>
                <AlertaFr></AlertaFr>
              </View>
            </View>
          </View>
        </View>
      </Page>
    </Document>
  );

  // funo que renderiza o PDF.
  function PrintPdf() {
    selectColor();
    return (
      <div>
        <PDFViewer
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            border: 0,
            width: '100%',
            height: '100%',
          }}
        >
          <MyDocument></MyDocument>
        </PDFViewer>
        <Button
          onClick={() => showList()}
          variant="success"
          style={{
            position: 'absolute',
            top: 50,
            left: 50,
            marginLeft: 5,
            marginTop: 5,
            marginBottom: 5,
            marginRight: 5,
          }}
        >
          VOLTAR
        </Button>
      </div>
    );
  }

  // eslint-disable-next-line
  if (viewPdf === 1) {
    return (
      <div className="fade-in">
        <PrintPdf></PrintPdf>
      </div>
    );
  } else {
    return <div></div>;
  }
}
export default Pdf;
