import React from 'react';
import axios from 'axios';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';

function Fix() {
  // endereos do server.
  var html = 'https://priority-server.herokuapp.com';
  //var html = '//localhost:3001';

  // dispatcher.
  const changeState = useDispatch();

  // chave para renderizao do componente.
  const viewCorrigir = useSelector((state) => state.corrigir);

  // login, setor e usurio.
  const loginUnidade = useSelector((state) => state.loginUnidade);
  const loginUsuario = useSelector((state) => state.loginUsuario);
  const loginSetor = useSelector((state) => state.loginSetor);

  // recuperando os dados do registro selecionado para correo.
  const i = useSelector((state) => state.itemId);
  var n = useSelector((state) => state.itemName);
  var d = useSelector((state) => state.itemDoc);
  var c = useSelector((state) => state.itemColor);
  var g = useSelector((state) => state.itemGrupo);
  var q = useSelector((state) => state.itemQueixa);
  var e = useSelector((state) => state.itemEntrada);
  var s = useSelector((state) => state.itemStatus);
  var news = useSelector((state) => state.itemNews);

  // funo que atualiza os dados.
  const clickConfirmarDados = () => {
    n = document.getElementById('inputName').value.toUpperCase();
    d = document.getElementById('inputDoc').value;
    // eslint-disable-next-line
    if (n == '' || d == '') {
      insertError();
    } else {
      deleteItemList(i);
      sendData();
    }
  };

  // alerta de erro.
  const insertError = () => {
    swal({
      title: 'ERRO',
      text: 'Favor preencher campo(s) em branco.',
      icon: 'warning',
      dangerMode: true,
    });
  };

  // funo que deleta o registro incorreto.
  const deleteItemList = (id) => {
    axios
      .get(html + '/deletepacientes/' + id)
      .then((res) => console.log(res.data));
  };

  // funo que insere o registro correto.
  const sendData = () => {
    var obj = {
      name: n,
      doc: d,
      color: c,
      grupo: g,
      queixa: q,
      entrada: e,
      status: s,
      news: news,
      unidade: loginUnidade,
      usuario: loginUsuario,
    };
    axios
      .post(html + '/insertpacientes', obj)
      .then((res) => console.log(res.data));
    setTimeout(function () {
      changeState({
        type: 'TRIGGER_ON',
        payloadUnidade: loginUnidade,
        payloadSetor: loginSetor,
        payloadUsuario: loginUsuario,
      });
    }, 1000);
  };

  // renderizao dos componentes (HTML).
  if (viewCorrigir === 1) {
    return (
      <div className="main fade-in">
        <p className="title2" style={{ marginBottom: 20 }}>
          CORRIGIR DADOS DE IDENTIFICAO
        </p>
        <div>
          <div>
            <label className="label">NOME COMPLETO</label>
          </div>
          <div>
            <input
              autoComplete="off"
              type="text"
              id="inputName"
              placeholder="NOME"
              defaultValue={n}
              className="input"
              style={{
                marginBottom: 20,
                width: 400,
                height: 50,
              }}
            ></input>
          </div>
          <div>
            <label className="label">DOCUMENTO</label>
          </div>
          <div>
            <input
              autoComplete="off"
              type="text"
              id="inputDoc"
              placeholder="DOC"
              defaultValue={d}
              className="input"
              style={{
                width: 200,
              }}
            ></input>
          </div>
        </div>
        <div className="main">
          <button
            onClick={() => clickConfirmarDados()}
            className="button-ok"
            tabIndex="-1"
            style={{
              marginTop: 30,
              width: 80,
            }}
          >
            OK
          </button>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Fix;
