import axios from 'axios';
import React, { useState, useEffect } from 'react';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.png';

function Login() {
  // endereos do server.
  var html = 'https://priority-server.herokuapp.com/login/';
  //var html = '//localhost:3001/login/';

  // dispatcher.
  const changeState = useDispatch();

  // chaves para renderizao dos componentes.
  const viewLogin = useSelector((state) => state.login);

  // estado que recebe um registro filtrado de login.
  const [dataLogin, setDataLogin] = useState([]);

  var login = null;
  var senha = null;

  useEffect(() => {
    if (viewLogin === 1) {
      // eslint-disable-next-line
      login = '';
      // eslint-disable-next-line
      senha = '';
      updateDados();
    }
  });

  // funo que busca um registro de login vlido no servidor.
  const updateDados = () => {
    login = document.getElementById('inputLogin').value;
    senha = document.getElementById('inputSenha').value;
    var quotes = "'";
    var obj = {
      usuario: login,
      senha: senha,
    };
    axios
      .post(html + quotes + login + quotes + '/' + quotes + senha + quotes, obj)
      .then((res) => {
        setDataLogin(res.data);
        //console.log(dataLogin); // >> no exponha os dados quando o app entrar em produo!
      });
  };

  // checagem e realizao do login.
  const sendData = () => {
    updateDados();
    setTimeout(function () {
      if (dataLogin.length === 1) {
        console.log('LOGIN EFETUADO COM SUCESSO');
        // gatilho para abertura do componente 'List' e atualizao das listas.
        changeState({
          type: 'REFRESH_LIST',
          payloadUnidade: dataLogin.map((login) => login.unidade),
          payloadUsuario: dataLogin.map((login) => login.usuario),
          payloadSetor: dataLogin.map((login) => login.setor),
        });
      } else {
        console.log('FALHA DE LOGIN');
        // alerta de erro.
        swal({
          title: 'ERRO',
          text: 'Login ou senha incorretos. Repita a operao.',
          icon: 'warning',
          dangerMode: true,
        });
        changeState({ type: 'LOGIN_OFF' });
        changeState({ type: 'LOGIN_ON' });
      }
    }, 4000);
  };

  // renderizao dos componentes (HTML).
  if (viewLogin === 1) {
    return (
      <div
        className=" main fade-in"
        style={{
          justifyContent: 'center',
          textAlign: 'center',
        }}
      >
        <img alt="" src={logo} style={{ height: 130, marginBottom: 50 }}></img>
        <p
          className="title2"
          style={{ width: 500, marginTop: 0, marginBottom: 20 }}
        >
          A SOLUO EM CLASSIFICAO DE RISCOS
        </p>
        <label className="label">LOGIN</label>
        <input
          onChange={() => updateDados()}
          className="input"
          type="text"
          id="inputLogin"
          autoComplete="off"
          style={{
            width: 200,
          }}
        ></input>
        <label className="label">SENHA</label>
        <input
          onChange={() => updateDados()}
          className="input"
          type="password"
          id="inputSenha"
          autoComplete="off"
          style={{
            width: 200,
          }}
        ></input>
        <button
          onClick={() => sendData()}
          className="button-ok"
          tabIndex="-1"
          style={{
            marginTop: 30,
            width: 80,
          }}
        >
          OK
        </button>
      </div>
    );
  } else {
    return null;
  }
}
export default Login;
