import React from 'react';
import moment from 'moment';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';

function Insert() {
  const changeState = useDispatch();

  const viewComponent = useSelector(state => state.inserir);
  const loginUnidade = useSelector(state => state.loginUnidade);
  const loginSetor = useSelector(state => state.loginSetor);
  const loginUsuario = useSelector(state => state.loginUsuario);

  var n = '';
  var d = '';
  var e = moment().format('DD/MM/YYYY - HH:mm:ss'); // retorna a data atual formatada.
  var s = 'PR-TRIADO';

  // funo que prepara os dados de identificao do paciente e ativa o componente 'InsertOptions'.
  const clickConfirmarDados = () => {
    n = document.getElementById('inputName').value.toUpperCase();
    d = document.getElementById('inputDoc').value.toUpperCase();
    // eslint-disable-next-line
    if (n == '' || d == '') {
      insertError();
    } else {
      changeState({
        type: 'INSERT_OPTIONS_ON',
        payloadName: n,
        payloadDoc: d,
        payloadEntrada: e,
        payloadStatus: s,
        payloadUnidade: loginUnidade,
        payloadSetor: loginSetor,
        payloadUsuario: loginUsuario
      });
    }
  };

  // alerta de erro.
  const insertError = () => {
    swal({
      title: 'ERRO',
      text: 'Favor preencher campo(s) em branco.',
      icon: 'warning',
      dangerMode: true
    });
  };

  if (viewComponent === 1) {
    return (
      <div className="main fade-in">
        <p className="title2" style={{ marginBottom: 20 }}>
          ADICIONAR PACIENTE
        </p>
        <label className="label">NOME COMPLETO</label>
        <input
          className="input"
          autoComplete="off"
          type="text"
          id="inputName"
          style={{
            width: 400,
            height: 50
          }}
        ></input>
        <label className="label">DOCUMENTO</label>
        <input
          className="input"
          autoComplete="off"
          type="text"
          id="inputDoc"
          style={{
            width: 200,
            height: 50
          }}
        ></input>
        <button
          onClick={() => clickConfirmarDados()}
          className="button-ok"
          tabIndex="-1"
          style={{
            marginTop: 30,
            width: 80
          }}
        >
          OK
        </button>
      </div>
    );
  } else {
    return null;
  }
}
export default Insert;
